<template>
  <div class="main-container">
    <div class="recharge-amount">
      <span v-html="paramsData.dmoHtml"></span>
      <div class="amount-container">
        <div class="amount">
          <span class="money-logo">&yen;</span>
          <span class="label">当前支付金额为：</span>
          <input
            v-model.number="paramsData.amount"
            placeholder="请输入金额"
            disabled
          />
        </div>
        <p class="prompt">请选择支付方式付款，完成支付</p>
      </div>
      <div class="payment-container">
        <h6 class="title">选择支付方式</h6>
        <div class="payment-type">
          <tab-list
            @emitTab="tabClick"
            :list="paymentList"
            :activeIndex="paramsData.activeIndex"
          ></tab-list>
          <div class="online-pay" v-show="paramsData.activeIndex === 0">
            <ul class="payment-list online-list">
              <li
                v-for="(item, index) in onlineTypeList"
                :key="index"
                :class="
                  paramsData.onlinePayment === item.onlineType ? 'active' : ''
                "
                @click="paymentClick('onlinePayment', item.onlineType)"
              >
                <svg class="icon" aria-hidden="true">
                  <use :xlink:href="item.svgName"></use>
                </svg>
              </li>
            </ul>
            <el-button type="primary" @click="submitPayment">去支付</el-button>
          </div>
          <div class="balance-pay" v-show="paramsData.activeIndex === 1">
            <ul class="payment-list balance-list">
              <li
                v-for="(item, index) in balanceTypeList"
                :key="index"
                :class="
                  paramsData.balancePayment === item.balanceType ? 'active' : ''
                "
                @click="paymentClick('balancePayment', item.balanceType)"
              >
                <svg class="icon" aria-hidden="true">
                  <use :xlink:href="item.svgName"></use>
                </svg>
                <p class="amount">
                  {{ item.word }}{{ paramsData.accountInfo.accountBalance }}
                </p>
              </li>
            </ul>
            <el-button type="primary" @click="submitPayment">去支付</el-button>
          </div>
          <div class="offline-pay" v-show="paramsData.activeIndex === 2">
            <el-alert
              title="您线下打款，数企在收到汇款后将自动充值到您的企业余额账户中。"
              type="warning"
              :closable="false"
            >
            </el-alert>
            <ul class="payment-list offline-list">
              <li>
                <p>
                  {{ paramsData.virtualAccount }}&nbsp;&nbsp;
                  <el-link type="primary" @click="handleCopy">复制</el-link>
                </p>
                <p>收款户名：八度云计算（安徽）有限公司</p>
                <p>收款银行：中国银行合肥望江中路支行</p>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
  <!--  支付二维码  -->
  <BuyQRCodeDialog
    v-model:visible="paramsData.paymentVisible"
    :amount="paramsData.amount"
    :paymentInfo="paramsData.paymentInfo"
  ></BuyQRCodeDialog>
</template>
<script lang="ts">
export default {
  name: "payment-buy",
};
</script>
<script lang="ts" setup>
import { reactive, defineAsyncComponent } from "vue";
import { ElMessage } from "element-plus";

import { useVueRouter } from "@/assets/useHook";
import { paymentEnum } from "@assets/typeStatus";

import { buyPaymentQRCodeApi } from "@api/payment-api";
import { queryAccountApi } from "@api/financial-statistics-api";
import { getCompanyVirtualAccountByAgencyIdApi } from "@api/financial-center-api";
import { copyText } from "jz-tool-lib";

const BuyQRCodeDialog = defineAsyncComponent(
  () => import("@components/payment/buy-QRCode-dialog.vue")
);

interface ParamsDataInter {
  dmoHtml: string;
  amount: undefined;
  paymentInfo: Record<string, any>;
  virtualAccount: string;
  onlinePayment: string;
  balancePayment: string;
  offlinePayment: any;
  accountInfo: any;
  activeIndex: number;
  rechargeImg: string;
  paymentVisible: boolean;
}

const { goPage, routeQuery } = useVueRouter();
const paramsData = reactive<ParamsDataInter>({
  dmoHtml: "",
  onlinePayment: "online-wx", // 线上支付-当前选择 默认选择微信支付
  balancePayment: "balancePay-qyb", // 账户余额支付-当前选择
  offlinePayment: "offline", // 线下支付-当前选择
  paymentInfo: {},
  virtualAccount: "",
  activeIndex: 0,
  rechargeImg: "",
  accountInfo: "",
  paymentVisible: false,
  amount: routeQuery.value.amount,
});
const paymentList = [
  {
    label: "在线支付",
    value: 0,
  },
  {
    label: "余额支付",
    value: 1,
  },
  {
    label: "线下支付",
    value: 2,
  },
];
const onlineTypeList = [
  {
    onlineType: "online-wx",
    svgName: "#icon-WeChat",
  },
  {
    onlineType: "online-zfb",
    svgName: "#icon-alipay",
  },
];
const balanceTypeList = [
  {
    balanceType: "balancePay-qyb",
    svgName: "#icon-qiYeBao",
    word: "可用余额：",
  },
];

// 获取虚拟账户信息
function getVirtualAccount() {
  getCompanyVirtualAccountByAgencyIdApi().then((res) => {
    paramsData.virtualAccount = res?.data.virtualAccount ?? "";
  });
}

// 复制粘贴
function handleCopy() {
  const text = `账号：${paramsData.virtualAccount}，收款户名：八度云计算（安徽）有限公司，收款银行：中国银行合肥望江中路支行`;
  copyText(text)
    .then(() => {
      ElMessage.success("复制成功");
    })
    .catch(() => {
      ElMessage.success("复制失败");
    });
}

// 线上支付 --- 选择支付方式
const paymentClick = (type, value) => {
  paramsData[type] = value;
};
const submitPayment = () => {
  if (paramsData.activeIndex === 1) {
    if (
      Number(paramsData.amount) > Number(paramsData.accountInfo.accountBalance)
    ) {
      ElMessage.error("您的账户余额不足。");
      return;
    }
  }
  paymentApi();
};
const paymentApi = () => {
  const params = paymentParams();
  buyPaymentQRCodeApi.getData(params).then((res) => {
    paramsData.paymentInfo = res.data;
    if (paramsData.activeIndex === 0) {
      paramsData.paymentVisible = true;
    } else if (paramsData.activeIndex === 1) {
      if (res.data.orderState === 1) {
        ElMessage.success(
          "您已有该商品订单，无法再次下单，请前往订单页完成支付。"
        );
      }
      goPage("/marketService/personalOrder/list");
    } else if (paramsData.activeIndex === 2) {
      if (res.data.orderState === 1) {
        ElMessage.success(
          "您已有该商品订单，无法再次下单，请前往订单页完成支付。"
        );
      } else {
        ElMessage.success("提交成功");
      }
      goPage("/marketService/personalOrder/list");
    }
  });
};
// 获取支付信息
const paymentParams = () => {
  const info = {
    seriesId: routeQuery.value.productId,
    agencyLevel: routeQuery.value.agencyLevel,
    durationYear: routeQuery.value.durationYear,
    paymentType: "",
    mode: 0,
    orderType: 0,
  };
  if (routeQuery.value.mode) {
    Reflect.set(info, "mode", Number(routeQuery.value.mode));
  }
  if (routeQuery.value.orderNo) {
    Reflect.set(info, "orderNo", routeQuery.value.orderNo);
  }
  if (routeQuery.value.orderType) {
    Reflect.set(info, "orderType", Number(routeQuery.value.orderType));
  }
  if (paramsData.activeIndex === 0) {
    info.paymentType = paymentEnum[paramsData.onlinePayment];
  }
  if (paramsData.activeIndex === 1) {
    info.paymentType = paymentEnum[paramsData.balancePayment];
  }
  if (paramsData.activeIndex === 2) {
    info.paymentType = paymentEnum[paramsData.offlinePayment];
    Reflect.set(info, "rechargeImg", paramsData.rechargeImg);
  }
  return info;
};
// tab 点击
const tabClick = (item) => {
  paramsData.activeIndex = item.value;
  if (item.value === 1) {
    getAccountBalance();
  }
};
// 获取账户总览
const getAccountBalance = async () => {
  const { data = {} } = await queryAccountApi.getData();
  paramsData.accountInfo = data;
};

// 已有订单，匹配到之前支付方式
if (routeQuery.value.modePayment && routeQuery.value.paymentType) {
  const paymentType = paymentEnum[Number(routeQuery.value.paymentType)];
  if (routeQuery.value.modePayment === "onlinePayment") {
    paramsData.activeIndex = 0;
    paramsData.onlinePayment = paymentType;
  } else if (routeQuery.value.modePayment === "balancePayment") {
    paramsData.activeIndex = 1;
    paramsData.balancePayment = paymentType;
    getAccountBalance();
  } else if (routeQuery.value.modePayment === "offlinePayment") {
    paramsData.activeIndex = 2;
    paramsData.offlinePayment = paymentType;
  }
}
getVirtualAccount();
</script>

<style lang="scss" scoped>
.recharge-amount {
  width: 1200px;
  margin: 0 auto;
}

.amount-container {
  height: 190px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 60px;
  background: #ffffff;
  border-radius: 8px;

  .amount {
    position: relative;
    display: flex;
    align-items: center;

    .money-logo {
      position: absolute;
      top: 5px;
      left: 215px;
      font-size: 28px;
      font-weight: 600;
      color: #f12525;
      line-height: 1;
    }

    .label {
      color: #242934;
      font-size: 26px;
      line-height: 36px;
    }

    input {
      width: 200px;
      height: 36px;
      color: #f12525;
      font-size: 27px;
      font-weight: 600;
      padding: 10px 10px 2px 30px;
      line-height: 1;
      border-bottom: 1px solid #e1e4e8;

      &::placeholder {
        font-size: 18px;
        color: #9ca6b9;
        line-height: 1;
        font-weight: normal;
      }
    }
  }

  .prompt {
    font-size: 14px;
    color: #9ca6b9;
    margin-top: 8px;
  }
}

.payment-container {
  padding: 0 60px 80px;
  margin-top: 20px;
  background: #ffffff;
  border-radius: 8px;

  .title {
    display: flex;
    align-items: center;
    height: 88px;
    font-size: 20px;
    color: #242934;
    line-height: 20px;
    border-bottom: 1px solid #edf1f6;
  }

  .payment-type {
    margin-top: 28px;

    .payment-list {
      display: flex;
      margin: 20px 0 40px;

      li {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 228px;
        height: 96px;
        cursor: pointer;
        margin-right: 12px;
        background: #ffffff;
        border-radius: 4px;
        border: 1px solid #e1e4e8;

        &:hover {
          border-color: #0056ff;
        }

        &.active {
          border-color: #0056ff;
        }

        &.active::before {
          position: absolute;
          right: 0;
          bottom: 0;
          content: "";
          width: 27px;
          height: 27px;
          background: url("../../assets/images/select-icon.png") no-repeat
            center;
          background-size: 100%;
        }
      }
    }

    .balance-list li {
      flex-direction: column;

      .amount {
        font-size: 14px;
        color: #9ca6b9;
        margin-top: 10px;
      }
    }

    .offline-list {
      li {
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-end;
        width: 350px;
        height: 190px;
        padding: 0 16px;
        border-radius: 8px;
        box-shadow: 0 8px 40px #646b7a26;
        border: none;
        background: url("@/assets/images/pay_zgyh.png") top 16px left 16px
          no-repeat;
        background-size: 50%;

        p {
          display: flex;
          align-items: center;
          font-size: 15px;
          margin-bottom: 5px;

          &:last-child {
            margin-bottom: 20px;
          }
        }
      }
    }

    .online-list,
    .balance-list {
      svg {
        width: 110px;
        height: 35px;
      }
    }
  }
}

.body-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  .QR-code {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 168px;
    height: 168px;
    margin-bottom: 40px;
    border: 1px solid #e1e4e8;
  }

  .word {
    font-size: 14px;
    color: #242934;
    line-height: 1;
  }

  .amount {
    font-size: 26px;
    font-weight: 600;
    color: #f12525;
    line-height: 1;
    margin: 12px 0 40px;
  }

  .prompt {
    font-size: 14px;
    color: #9ca6b9;
    line-height: 1;
  }
}

.offline-pay {
  .custom-upload-img {
    width: 50%;
    margin-bottom: 40px;
  }
}
</style>
<style lang="scss">
.payment-dialog {
  .el-dialog__body {
    padding: 0;
  }

  .el-dialog__footer {
    padding: 40px 0 50px;
  }
}
</style>
